













import Vue from 'vue'

export default Vue.extend({
  name: 'login',
  data () {
    return {
      username: '',
      password: ''
    }
  },
  methods: {
    async login (e: Event) {
      e.preventDefault()
      this.$store.dispatch('LOGIN', {
        username: this.username,
        password: this.password
      })
        .then(() => {
          alert('로그인 성공')
          this.$router.push('/')
        })
        .catch((err) => {
          console.error(err)
          alert('로그인 실패')
        })
    }
  },
  computed: {
    payload () {
      return this.$store.state.payload
    }
  }
})
